"use strict";import e from"crypto-js/aes";import t from"crypto-js/tripledes";import r from"crypto-js/md5";import c from"crypto-js/sha1";import n from"crypto-js/enc-base64";import p from"crypto-js/enc-utf8";import o from"crypto-js/mode-ecb";import s from"crypto-js/pad-pkcs7";let a={AES:e,DES:t,MD5:r,SHA1:c,enc:{Utf8:p,Base64:n},mode:{ECB:o},pad:{Pkcs7:s}};export function aesEncrypt(e,t){try{let r=a.enc.Utf8.parse(e),c=a.enc.Utf8.parse(t);return a.AES.encrypt(c,r,{mode:a.mode.ECB,padding:a.pad.Pkcs7}).toString()}catch(e){return""}}export function aesDecrypt(e,t){try{let r=a.enc.Utf8.parse(e),c=a.enc.Base64.parse(t);return a.AES.decrypt({ciphertext:c},r,{mode:a.mode.ECB,padding:a.pad.Pkcs7}).toString(a.enc.Utf8)}catch(e){return""}}export function desEncrypt(e,t){try{let r=a.enc.Utf8.parse(e),c=a.enc.Utf8.parse(t);return a.DES.encrypt(c,r,{mode:a.mode.ECB,padding:a.pad.Pkcs7}).toString()}catch(e){return""}}export function desDecrypt(e,t){try{let r=a.enc.Utf8.parse(e),c=a.enc.Base64.parse(t);return a.DES.decrypt({ciphertext:c},r,{mode:a.mode.ECB,padding:a.pad.Pkcs7}).toString(a.enc.Utf8)}catch(e){return""}}export function encodeBase64(e){return a.enc.Base64.stringify(a.enc.Utf8.parse(e))}export function decodeBase64(e){return a.enc.Utf8.stringify(a.enc.Base64.parse(e))}export function md5Encrypt(e){return a.MD5(e).toString()}export function sha1Encrypt(e){return a.SHA1(e).toString()}export default{aesEncrypt:aesEncrypt,aesDecrypt:aesDecrypt,desEncrypt:desEncrypt,desDecrypt:desDecrypt,encodeBase64:encodeBase64,decodeBase64:decodeBase64,md5Encrypt:md5Encrypt,sha1Encrypt:sha1Encrypt};